.about {
  background: #fff5f6;
  padding-bottom: 98px;
  padding-top: 121px;
  @media screen and (min-width: 768px) {
    padding-bottom: 122px;
    padding-top: 112px;
  }
  @media screen and (min-width: 1200px) {
    padding-bottom: 39px;
    padding-top: 125px;
  }
}
// .container {
//   padding-left: 0px;
//   padding-right: 0px;
// }

.about-subtitle {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
  color: $secondary-title-color;
  padding-bottom: 15px;
}
.about-title {
  padding-bottom: 106px;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;
}
.about-box {
  display: flex;
  position: relative;
}
.overlay {
  // position: relative;
  // min-height: 100vh;
  max-width: 100%;
  // max-height: 400px;
  margin-left: auto;
  margin-right: auto;
  background-image: url('../images/milk-mob-1x-min.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url('../images/milk-mob-2x-min.png');
  }
  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1200px) {
    max-width: 1600px;
    // max-height: 600px;
    background-image: url('../images/milk-des-1x-min.png');
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/milk-des-2x-min.png');
    }
  }
}
.about-img {
  top: -47px;
  position: absolute;
}
.about-list {
  width: 463px;
  margin-left: 74px;
  display: flex;
  flex-direction: column;
  margin-right: 20px;
  margin-left: auto;

  // justify-content: flex-start;
  // flex-wrap: wrap;
  //   // text-align: center;
  // margin-bottom: 60px;
}

.about-list-item {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 176%;
  letter-spacing: 0.04em;
  color: #000000;
  padding-bottom: 35px;
}
.about-text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 194%;
  letter-spacing: 0.04em;
  color: #907e82;
  padding-bottom: 22px;
}
.about-text-quote {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 194%;
  letter-spacing: 0.04em;
  color: #907e82;
  padding-bottom: 45px;
}
.about-btn {
  width: 176px;
  height: 44px;
  background: #ffffff;
  // box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border: 0;
  border-radius: 22px;
  // margin-top: 30px;
  margin-right: auto;
  // margin-left: auto;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.87;
  letter-spacing: 0.06em;
  cursor: pointer;
  display: flex;
  padding: 10px 37px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}
.about-btn-text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 21px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #d41443;
}
